.commons-square {
    div {
        width: 5px;
        height: 5px;
        background: $primary; } }
.commons-square-gray {
    div {
        width: 5px;
        height: 5px;
        background: $gray; } }
.commons-square-white {
    div {
        width: 5px;
        height: 5px;
        background: $white;
        margin-top: 7px; } }

.collapse-one {
    display: none;

    &__header {
        background: $light-gray;
        &__quad {
            width: 15px;
            height: 15px;
            background: $dark-gray;
            margin-top: 46px;
            margin-left: -45px;
            position: absolute;
            @include respond-to(laptop) {
                margin-left: -45px; }
            @include respond-to(tablet) {
                margin-left: -35px; }
            @include respond-to(phone-land) {
                margin-left: -5px; } }

        .row {
            padding: 40px 0px 40px 20px;
            border-left: 1px solid $dark-gray;
            margin-left: -38px;
            @include respond-to(tablet) {
                margin-left: -28px; }
            @include respond-to(phone-land) {
                margin-left: 1px; } } }
    &__header-city {
        .dark-gray {
            font-size: 12px; }
        &__border-left {
            padding: 60px 0px 60px 40px;
            border-left: 1px solid $dark-gray;
            margin-left: -38px;
            @include respond-to(tablet) {
                margin-left: -28px; }
            @include respond-to(phone-land) {
                margin-left: 1px; }
            &__quad {
                width: 15px;
                height: 15px;
                background: $dark-gray;
                margin-top: -48px;
                margin-left: -48px;
                position: absolute;
                @include respond-to(laptop) {
                    margin-left: -48px; }
                @include respond-to(tablet) {
                    margin-left: -48px; }
                @include respond-to(phone-land) {
                    margin-left: -48px; } }
            &__red {
                div {
                    width: 15px;
                    height: 15px;
                    background: $primary;
                    margin-top: 2px; } }
            &__gray {
                div {
                    width: 15px;
                    height: 15px;
                    background: $gray;
                    margin-top: 2px; } }
            &__black {
                div {
                    width: 15px;
                    height: 15px;
                    background: $black;
                    margin-top: 2px; } } } }
    &__delivery {
        background: $light-gray;
        .dark-gray {
            font-size: 12px; }
        &__border-left {
            padding: 60px 0px 60px 40px;
            border-left: 1px solid $dark-gray;
            margin-left: -38px;
            @include respond-to(tablet) {
                margin-left: -28px; }
            @include respond-to(phone-land) {
                margin-left: 1px; }
            &__quad {
                width: 15px;
                height: 15px;
                background: $dark-gray;
                margin-top: -48px;
                margin-left: -48px;
                position: absolute;
                @include respond-to(laptop) {
                    margin-left: -48px; }
                @include respond-to(tablet) {
                    margin-left: -48px; }
                @include respond-to(phone-land) {
                    margin-left: -48px; } } } }
    &__payment {
        background: $black;
        padding: 60px 0px; } }

.assembly-instructions {
    &__header {
        background-image: linear-gradient(250deg,transparent -18.91%,rgba(0, 0, 0, 0.82) 57.57%),url(../img/review/arjay.png);
        background-repeat: round;
        height: 510px; }
    &__list-item {
        margin-left: -12px;
        text-decoration: none;
        @include respond-to(phone-land) {
            margin-left: 5px; }
        &:hover {
            text-decoration: none;
            color: $gray;
            i {
                transition: .3s;
                color: $primary; } }
        &__icon {
            font-size: 6px;
            color: $dark-gray; } }
    &__list-item-red {
        margin-left: -12px;
        text-decoration: none;
        @include respond-to(phone-land) {
            margin-left: 0px; }
        &:hover {
            text-decoration: none;
            color: $primary;
            i {
                transition: .3s;
                color: $primary; } }
        &__icon {
            font-size: 6px;
            color: $dark-gray; }
        &__icon-pdf {
            font-size: 14px;
            color: $dark-gray; } } }
