@import '../../sass/_vars';
@import '../../sass/_mixins/_media';

.site-header {
    height: 150px;
    @include respond-to(tablet) {
        height: 133px; }
    @include respond-to(phone-land) {
        height: 50px; } }

.cart-overlay {
    display: block;
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: $gray;
    opacity: 0;
    z-index: 33;
    cursor: pointer;
    transition: $transition;
    visibility: hidden; }

.show-cart-overlay {
    visibility: visible;
    opacity: 0.8; }

.price-overlay {
    z-index: 444; }

.top-bar {
    color: $gray;
    border-bottom: 1px solid $light-gray;
    z-index: 34;

    @include respond-to(phone-land) {
        height: 50px;
        display: flex;
        align-content: center; }

    i {
        transition: $transition;
        font-size: 18px; }

    &__compare,
    &__favorite,
    &__email {
        color: $gray;
        transition: $transition;

        &:hover {
            color: $gray; }

        &:hover i {
            color: $primary; } }

    &__phone {
        color: $black;

        &:hover {
            color: $black; } }

    &__logo {
        width: 100px;

        @include respond-to(phone-land) {
            display: flex;
            margin-top: 10px;
            max-width: 70px;
            position: absolute;
            right: 0; } }

    &__search-btn {
        color: $gray;
        border: none;
        background: none;

        &:hover {
            color: $primary;
            cursor: pointer; } }

    &__cart-amount {
        font-size: 16px;
        color: $black; }

    &__cart_total-amount {
        font-size: 16px;
        font-weight: bold; }

    &__search-box_hidden {
        z-index: 23;
        padding: 13px 15px;
        box-shadow: 0px 6px 20px -13px rgba(3,3,3,.1);
        display: flex;
        left: 120px;
        right: 112px;
        max-width: 80%;
        visibility: hidden;
        opacity: 0;
        transition: all 2s easy;

        button,
        input {
            color: $gray;
            border: none;
            outline: none;
            background: none; }

        input {
            color: $black;
            width: 100%;

            &:focus {} }

        i {
            font-size: 18px;
            cursor: pointer; }

        .fa-search {
            color: $primary; }

        .fa-times {
            font-size: 20px;
            color: $gray;

            &:hover {
                color: $primary; } } }

    &__search-box_show {
        visibility: visible;
        opacity: 1; }


    &__hamburger-menu {
        margin-top: 15px;
        cursor: pointer;

        div {
            height: 3px;
            width: 25px;
            margin-bottom: 4px;
            background: $gray; }

        .bar2 {
            width: 15px; } }

    &__cart_button {
        z-index: 223;
        color: $gray;
        cursor: pointer; }

    &__circle-badge {
        height: 20px;
        width: 20px;
        border-radius: 50%;
        background: $primary;
        border: none;
        top: -10px;
        right: -10px;
        font-size: 11px; } }


.site-header__nav {
    padding: 104px 0 9px;
    font-weight: 500;
    z-index: 2;

    @include respond-to(tablet) {
        padding: 0; }

    &_square {
        transition: $transition;
        font-size: 6px;
        opacity: 0; }

    &-item {
        margin-right: 8px;
        color: $primary;
        transition: $transition;
        position: relative;

        &__catalog_hover {
            display: block;
            visibility: hidden;
            transition: all $transition ease;
            opacity: 0;
            position: absolute;
            top: -5px;
            left: -25px;
            background: $white;
            z-index: 20; }

        &:hover .site-header__nav_square {
            opacity: 1; }

        .active-link, &:hover .site-header__nav-link {
            color: $primary; }

        .site-header__nav_square {
            margin-left: 5px; }

        &:hover &__catalog_hover {
            visibility: visible;
            opacity: 1; } }

    &-link {
        color: $dark-gray;
        transition: $transition; }

    .caltalog-link {
        z-index: 33; } }

.active-link .site-header__nav_square {
    opacity: 1; }
