.half-height {
    height: 60vh;
    @include respond-to(phone-land) {
        height: 80vh; } }

.actions-slide1 {
    background-image: url('../img/actions/first-slide.png');
    @include respond-to(phone-land) {
        background-image: url('../img/actions/first-slide-mobile.png'); }

    &__title {
        width: 50%;
        @include respond-to(phone-land) {
            width: 100%; } }

    &__description {
        width: 50%;
        @include respond-to(phone-land) {
            width: 100%; } }

    &__rectangle {
        left: 0;
        max-width: 50%;
        height: 100%;
        @include respond-to(phone-land) {
            background-image: url('../img/actions/first-slide-mobile.png');
            transform: rotate(90deg);
            max-width: 100%;
            top: 0; } }

    &__exclamation-point {
        background: rgba(228, 228, 228, 0.20);
        width: 20px;
        height: 20px;
        border-radius: 50%; }

    &__cart {
        right: 0;
        max-width: 50%;
        @include respond-to(phone-land) {
            bottom: 0;
            max-width: 100%; } } }

.actions-slide2 {
    background-image: url('../img/actions/second-slide.png');
    @include respond-to(phone-land) {
        background-image: url('../img/actions/second-slide-mobile.png'); }

    &__text {
        margin-bottom: 200px; } }

.actions-slide3 {
    background-image: url('../img/actions/slide3.png');
    background-position: left;
    @include respond-to(phone-land) {
        background-image: url('../img/actions/slide3-mobile.png'); }

    &__text {
        width: 50%;
        @include respond-to(laptop) {
            width: 80%; } } }

.actions-slide4 {
    background-image: url('../img/actions/slide4.png');
    @include respond-to(phone-land) {
        background-image: url('../img/actions/slide4-mobile.png'); }

    &__text {
        width: 50%;
        @include respond-to(laptop) {
            width: 80%; }
        @include respond-to(phone-land) {
            margin-bottom: 100px;
            width: 100%; } } }

.actions-slide5 {
    background-color: $gray;
    background-image: url('../img/actions/slide5.png');
    @include respond-to(phone-land) {
        background-image: url('../img/actions/slide5-mobile.png'); }
    &__text {
        font-size: 1.5rem; } }

.actions-slide6 {
    background-color: $black;
    background-image: url('../img/actions/slide6.png');
    background-position: left;
    @include respond-to(laptop) {
        background-position: bottom; }

    @include respond-to(phone-land) {
        background-image: url('../img/actions/slide6-mobile.png'); }
    &__text {
        font-size: 1.5rem; } }

.actions-sidebar {
    min-width: 280px;

    .filter {
        padding: 30px 20px; }

    .filter-content li:first-child i {
        cursor: pointer;
        font-size: 14px;
        border: 2px solid $light-gray;
        color: $primary; }

    .filter-content li:first-child {
        color: $primary; }

    .fa-square {
        border: 2px solid $light-gray; } }




//////////// Slider Price Range ///////////
input[type=range] {
  -webkit-appearance: none;
  margin: 10px 0;
  width: 100%; }

input[type=range]:focus {
  outline: none; }

input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 3px;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
  background: $primary;
  border-radius: 25px;
  border: 0px solid #000101; }

input[type=range]::-webkit-slider-thumb {
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
  border: 0px solid #000000;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background: $primary;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -8px; }

input[type=range]:focus::-webkit-slider-runnable-track {
  background: $primary; }

input[type=range]::-moz-range-track {
  width: 100%;
  height: 3px;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
  background: $primary;
  border-radius: 25px;
  border: 0px solid #000101; }

input[type=range]::-moz-range-thumb {
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
  border: 0px solid #000000;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background: $primary;
  cursor: pointer; }

input[type=range]::-ms-track {
  width: 100%;
  height: 3px;
  cursor: pointer;
  animate: 0.2s;
  background: transparent;
  border-color: transparent;
  border-width: 20px 0;
  color: transparent; }

input[type=range]::-ms-fill-lower {
  background: $primary;
  border: 0px solid #000101;
  border-radius: 50px;
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d; }

input[type=range]::-ms-fill-upper {
  background: $primary;
  border: 0px solid #000101;
  border-radius: 50px;
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d; }

input[type=range]::-ms-thumb {
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
  border: 0px solid #000000;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background: $primary;
  cursor: pointer; }

input[type=range]:focus::-ms-fill-lower {
  background: $primary; }

input[type=range]:focus::-ms-fill-upper {
  background: $primary; }


//////////  End of price range /////////

.filter-price-range {
    .price-value {
        background: $light-gray;
        padding: 8px 10px; } }


.product-info {
    p {
        padding-bottom: 10px; } }

.hidden {
    display: none; }


.active {
    color: $primary; }

.actions-pagination {
    &__turn-left,
    &__turn-right {
        height: 25px;
        width: 25px;
        background: $gray;
        color: $white; } }
