@import '../../sass/_vars';

.cart-wrapper {
    display: none;
    padding: 60px 40px 40px;
    background: $white;
    font-size: 14px;
    visibility: hidden;
    transition: $transition;

    .cart__item {
        height: 100px;

        &_image {
            width: 60px; }

        &_info {
            min-width: 173px; }

        &_price {
            width: 115px;

            &_control-buttons {}

            &_count {
                height: 24px;
                width: 50px;
                font-weight: lighter;
                background: $light-gray; }

            &_add,
            &_reduce {
                height: 24px;
                width: 24px;
                background: lighten($gray, 14%);
                color: $white;
                font-size: 14px;
                border: none; }

            &_add {
                background: $primary;
                font-size: 15px; } }

        &_price_money {
            font-size: 33px;
            text-align: center;
            font-weight: 500; }

        &__title,
        &__description {
            font-size: 14px;
            cursor: pointer; } }

    .cart__total-info {
        font-size: 18px;
        margin-bottom: 40px;

        &_product_price {
            font-size: 33px;
            font-weight: 500;
            line-height: 0; } }

    .cart__buttons {
        .outline-white-button {
            color: $gray;
            border-color: $gray; }

        &_go-to-cart,
        &_buy-by-click {
            padding: 13px 23px; }

        &_go-to-cart {
            color: $primary;
            border: 1px solid $primary; }

        &_buy-by-click {
            color: $gray;
            border: 1px solid $gray; } } }

.cart-wrapper_visible {
    display: flex;
    visibility: visible;
    position: absolute;
    top: -21px;
    right: 37px;
    width: 500px;
    z-index: 37; }
