    font-size: 2rem;
  }
}

@media only screen and (max-width: 575px) {
  h1, .h1 {
    font-size: 1.8rem;
  }
}

h2, .h2 {
  font-size: 1.6rem;
}

@media only screen and (max-width: 1366px) {
  h2, .h2 {
    font-size: 1.6rem;
  }
}

@media only screen and (max-width: 575px) {
  h2, .h2 {
    font-size: 1.5rem;
  }
}

@media only screen and (max-width: 991px) {
  h4, .h4 {
    font-size: 1.4rem;
  }
}

@media only screen and (max-width: 767px) {
  h4, .h4 {
    font-size: 1.2rem;
  }
}

@media only screen and (max-width: 991px) {
