    height: 50px;
  }
}

.cart-overlay {
  display: block;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #B6C0CB;
  opacity: 0;
  z-index: 33;
  cursor: pointer;
  transition: 300ms;
  visibility: hidden;
}

.show-cart-overlay {
  visibility: visible;
  opacity: 0.8;
}

.price-overlay {
  z-index: 444;
}

.top-bar {
  color: #B6C0CB;
  border-bottom: 1px solid #F4F7FB;
  z-index: 34;
}

@media only screen and (max-width: 767px) {
  .top-bar {
    height: 50px;
    display: flex;
    align-content: center;
  }
}

.top-bar i {
  transition: 300ms;
  font-size: 18px;
}

.top-bar__compare, .top-bar__favorite, .top-bar__email {
  color: #B6C0CB;
  transition: 300ms;
}

.top-bar__compare:hover, .top-bar__favorite:hover, .top-bar__email:hover {
  color: #B6C0CB;
}

.top-bar__compare:hover i, .top-bar__favorite:hover i, .top-bar__email:hover i {
  color: #FE3362;
}

.top-bar__phone {
  color: #26323F;
}

.top-bar__phone:hover {
  color: #26323F;
}

.top-bar__logo {
  width: 100px;
}

@media only screen and (max-width: 767px) {
  .top-bar__logo {
    display: flex;
    margin-top: 10px;
    max-width: 70px;
    position: absolute;
    right: 0;
  }
}

.top-bar__search-btn {
  color: #B6C0CB;
  border: none;
  background: none;
}

.top-bar__search-btn:hover {
  color: #FE3362;
  cursor: pointer;
}

.top-bar__cart-amount {
  font-size: 16px;
  color: #26323F;
}

.top-bar__cart_total-amount {
  font-size: 16px;
  font-weight: bold;
}

.top-bar__search-box_hidden {
  z-index: 23;
  padding: 13px 15px;
  box-shadow: 0px 6px 20px -13px rgba(3, 3, 3, 0.1);
  display: flex;
  left: 120px;
  right: 112px;
  max-width: 80%;
  visibility: hidden;
  opacity: 0;
  transition: all 2s easy;
}

.top-bar__search-box_hidden button,
.top-bar__search-box_hidden input {
  color: #B6C0CB;
  border: none;
  outline: none;
  background: none;
}

.top-bar__search-box_hidden input {
  color: #26323F;
  width: 100%;
}

.top-bar__search-box_hidden i {
  font-size: 18px;
  cursor: pointer;
}

.top-bar__search-box_hidden .fa-search {
  color: #FE3362;
}

.top-bar__search-box_hidden .fa-times {
  font-size: 20px;
  color: #B6C0CB;
}

.top-bar__search-box_hidden .fa-times:hover {
  color: #FE3362;
}

.top-bar__search-box_show {
  visibility: visible;
  opacity: 1;
}

.top-bar__hamburger-menu {
  margin-top: 15px;
  cursor: pointer;
}

.top-bar__hamburger-menu div {
  height: 3px;
  width: 25px;
  margin-bottom: 4px;
  background: #B6C0CB;
}

.top-bar__hamburger-menu .bar2 {
  width: 15px;
}

.top-bar__cart_button {
  z-index: 223;
  color: #B6C0CB;
  cursor: pointer;
}

.top-bar__circle-badge {
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background: #FE3362;
  border: none;
  top: -10px;
  right: -10px;
  font-size: 11px;
}

.site-header__nav {
  padding: 104px 0 9px;
  font-weight: 500;
  z-index: 2;
}

@media only screen and (max-width: 991px) {
  .site-header__nav {
    padding: 0;
  }
}

.site-header__nav_square {
  transition: 300ms;
  font-size: 6px;
  opacity: 0;
}

.site-header__nav-item {
  margin-right: 8px;
  color: #FE3362;
  transition: 300ms;
  position: relative;
}

.site-header__nav-item__catalog_hover {
  display: block;
  visibility: hidden;
  transition: all 300ms ease;
  opacity: 0;
  position: absolute;
  top: -5px;
  left: -25px;
  background: #ffffff;
  z-index: 20;
}

.site-header__nav-item:hover .site-header__nav_square {
  opacity: 1;
}

.site-header__nav-item .active-link, .site-header__nav-item:hover .site-header__nav-link {
  color: #FE3362;
}

.site-header__nav-item .site-header__nav_square {
  margin-left: 5px;
}

.site-header__nav-item:hover .site-header__nav-item__catalog_hover {
  visibility: visible;
  opacity: 1;
}

.site-header__nav-link {
  color: #848E99;
  transition: 300ms;
}

.site-header__nav .caltalog-link {
  z-index: 33;
}

.active-link .site-header__nav_square {
  opacity: 1;
}

.cart-wrapper {
  display: none;
  padding: 60px 40px 40px;
  background: #ffffff;
  font-size: 14px;
  visibility: hidden;
  transition: 300ms;
}

.cart-wrapper .cart__item {
  height: 100px;
}

.cart-wrapper .cart__item_image {
  width: 60px;
}

.cart-wrapper .cart__item_info {
  min-width: 173px;
}

.cart-wrapper .cart__item_price {
  width: 115px;
}

.cart-wrapper .cart__item_price_count {
  height: 24px;
  width: 50px;
  font-weight: lighter;
  background: #F4F7FB;
}

.cart-wrapper .cart__item_price_add, .cart-wrapper .cart__item_price_reduce {
  height: 24px;
  width: 24px;
  background: #e0e4e9;
  color: #ffffff;
  font-size: 14px;
  border: none;
}

.cart-wrapper .cart__item_price_add {
  background: #FE3362;
  font-size: 15px;
}

.cart-wrapper .cart__item_price_money {
  font-size: 33px;
  text-align: center;
  font-weight: 500;
}

.cart-wrapper .cart__item__title, .cart-wrapper .cart__item__description {
  font-size: 14px;
  cursor: pointer;
}

.cart-wrapper .cart__total-info {
  font-size: 18px;
  margin-bottom: 40px;
}

.cart-wrapper .cart__total-info_product_price {
  font-size: 33px;
  font-weight: 500;
  line-height: 0;
}

.cart-wrapper .cart__buttons .outline-white-button {
  color: #B6C0CB;
  border-color: #B6C0CB;
}

.cart-wrapper .cart__buttons_go-to-cart, .cart-wrapper .cart__buttons_buy-by-click {
  padding: 13px 23px;
}

.cart-wrapper .cart__buttons_go-to-cart {
  color: #FE3362;
  border: 1px solid #FE3362;
}

.cart-wrapper .cart__buttons_buy-by-click {
  color: #B6C0CB;
  border: 1px solid #B6C0CB;
}

.cart-wrapper_visible {
  display: flex;
  visibility: visible;
  position: absolute;
  top: -21px;
  right: 37px;
  width: 500px;
  z-index: 37;
}

.product-catalog__list {
  list-style: none;
  padding: 70px 0 40px;
  min-width: 400px;
  height: 100%;
}

.product-catalog__item:hover .product-catalog__link_hover {
  visibility: visible;
  opacity: 1;
}

.product-catalog__item:hover .product-catalog__link {
  color: #FE3362;
}

.product-catalog__item_active {
  color: #FE3362;
}

.product-catalog__link {
  color: #26323F;
  padding-right: 3rem;
  padding-left: 3rem;
  transition: 300ms;
}

.product-catalog__link_icon {
  width: 30px;
  display: inline-block;
  text-align: center;
}

.product-catalog__link_hover {
  position: absolute;
  transform: translate(401px, -68px);
  background: white;
  visibility: hidden;
  opacity: 0;
  top: 69px;
  transition: all 300ms easy;
}

.product-catalog__link_hover:hover + .product-catalog__link {
  color: #FE3362;
}

.product-catalog__link:hover {
  color: #FE3362;
}

.second-level-product-catalog__list {
  list-style: none;
  padding: 30px 0;
  min-width: 400px;
  height: 664px;
  padding: 70px 0 40px;
}

.second-level-product-catalog__link {
  color: #848E99;
  padding-right: 3rem;
  padding-left: 3rem;
  transition: 300ms;
}

.second-level-product-catalog__link:hover {
  color: #FE3362;
}

.outline-button,
.outline-white-button,
.filled-button,
.filled-gray-button,
.button-filled {
  font-size: 14px;
  outline: none;
  width: 200px;
  height: 40px;
  background: none;
  border: 1px solid;
}

@media only screen and (max-width: 575px) {
  .outline-button,
  .outline-white-button,
  .filled-button,
  .filled-gray-button,
  .button-filled {
    width: 100%;
  }
}

.outline-button {
  border: 1px solid #FE3362;
  color: #FE3362;
}

.outline-white-button {
  border: 1px solid #ffffff;
  color: #ffffff;
}

.filled-button {
  border: 1px solid #FE3362;
  color: #FE3362;
}

.filled-gray-button {
  border: 1px solid #B6C0CB;
  color: #B6C0CB;
}

.add-remove {
  height: 24px;
  width: 24px;
  color: #ffffff;
  font-size: 14px;
  border: none;
}

.predominantly-card {
  box-shadow: 0 4px 15px -8px #eaeaea;
  min-height: 200px;
}

.predominantly-card__top-line {
  height: 2px;
  width: 40px;
  background: #FE3362;
}

.form-control {
  border: none !important;
  height: 40px;
  border-radius: 0 !important;
  outline: none;
  font-size: 14px;
}

.form-control:focus {
  box-shadow: none;
}

.course__red {
  background: #FE3362;
}

.course__red__color-white {
  color: #ffffff;
}

.news-card__size-text {
  font-size: 12px;
  color: #B6C0CB;
}

.news-card__img {
  max-width: 190px;
}

@media only screen and (max-width: 767px) {
  .news-card__img {
    width: 140px;
  }
}

@media only screen and (max-width: 991px) {
  .news-card__img {
    width: 140px;
  }
}

.news-card__body {
  margin-top: 30px;
}

@media only screen and (max-width: 767px) {
  .news-card__body {
    align-self: center;
    margin-top: 0px;
  }
}

@media only screen and (max-width: 991px) {
  .news-card__body {
    align-self: center;
    margin-top: 0px;
  }
}

.news-card__body__button {
  position: absolute;
  bottom: 7px;
  right: 23px;
  background: none;
  border: none;
  font-size: 25px;
  color: #F4F7FB;
}

.news-card-simple {
  box-shadow: 0 4px 15px -8px #eaeaea;
  color: #26323f;
  width: 100%;
  height: 190px;
}

@media only screen and (max-width: 575px) {
  .news-card-simple {
    height: 100%;
  }
}

.news-card-simple:hover {
  cursor: pointer;
}

.news-card-simple:hover button {
  color: #FE3362;
}

.working-way__background {
  font-size: 140px;
  opacity: 0.14;
}

.working-way__steps {
  font-weight: 500;
}

.working-way span {
  font-weight: 400;
}

@media only screen and (max-width: 767px) {
  .working-way__row {
    overflow-x: scroll;
  }
}

.working-way__content {
  bottom: 50px;
  width: 200px;
  left: 0;
}

.product-card-simple,
.product-card-horizontal {
  box-shadow: 0 4px 15px -8px #eaeaea;
  height: 300px;
  color: #26323F;
}

@media only screen and (max-width: 575px) {
  .product-card-simple,
  .product-card-horizontal {
    height: 200px;
  }
}

.product-card-simple:hover .info__button,
.product-card-horizontal:hover .info__button {
  color: #FE3362;
}

.product-card-simple .info__button,
.product-card-horizontal .info__button {
  background: none;
  border: none;
  color: #B6C0CB;
  transition: 300ms;
}

@media only screen and (max-width: 575px) {
  .product-card-simple {
    flex-direction: row-reverse;
  }
}

.product-card-horizontal .info {
  flex: 1;
}

.product-card-horizontal .info__button {
  right: 37px;
}

.product-card-horizontal__photo {
  flex: 2;
}

@media only screen and (max-width: 767px) {
  .product-card-horizontal__photo img {
    width: 100%;
  }
}

.action-card {
  background: #ffffff;
  padding: 20px;
  cursor: pointer;
  margin-bottom: 100px;
  height: 444px;
  min-width: 200px;
}

.action-card .visible-content {
  box-shadow: 0 4px 15px -8px #eaeaea;
}

.action-card .header__badge {
  color: #ffffff;
  width: 90px;
  height: 20px;
  transform: translateX(-20px);
}

.action-card .header__actions a i {
  transition: 300ms;
}

.action-card__title a {
  color: #26323F;
}

.action-card__img-wrapper {
  width: 100%;
}

.action-card__img {
  max-width: 100%;
  margin: 0 auto;
  height: 210px;
  object-fit: contain;
}

.action-card__img .add-to-cart {
  background: none;
  border: none;
  height: 30px;
}

.action-card__img .add-to-cart:hover {
  color: #FE3362;
}

.action-card .hover-content {
  opacity: 0;
  left: 0;
  right: 0;
  height: 100px;
  background: #ffffff;
  padding: 0 20px;
  transform: translateY(-20px);
}

.action-card:hover .hover-content {
  animation: hover-content 300ms cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  opacity: 1;
}

@keyframes hover-content {
  0% {
    transform: translateY(-20px);
  }
  100% {
    transform: translateY(0px);
  }
}

.action-card .horizontal-content__text {
  flex: 1;
}

.action-card .horizontal-content__img {
  flex: 2;
}

.prices {
  min-width: 75px;
}

.prices__old-price {
  text-decoration: line-through;
}

.prices__new-price {
  font-size: 24px;
}

.view-more:hover {
  color: #FE3362;
}

.action-card .content {
  height: 165px;
}

@media only screen and (max-width: 991px) {
  .action-card .content {
    flex-direction: row-reverse;
  }
}

.action-card .content__img-wrapper {
  flex: 1 1 0px;
}

.action-card .content__text {
  flex: 1 1 0px;
}

.action-card .content__buttons {
  flex: 1 1 0px;
  min-width: 240px;
}

.action-card .content__counting {
  width: 150px;
  height: 25px;
}

.action-card .content__cart-button {
  background: #FE3362;
  color: #ffffff;
  height: 40px;
  width: 40px;
}

@media only screen and (max-width: 991px) {
  .action-card .content__info {
    font-size: 12px;
  }
}

body {
  font-family: "Museo Sans Cyrl", sans-serif;
  line-height: 1.4;
  margin: 0;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.5;
  color: #333B4E;
  text-align: left;
  background-color: #ffffff;
}

.shadow {
  box-shadow: 0 4px 15px -8px #eaeaea;
}

.shadow-light {
  box-shadow: 0px 6px 16px rgba(169, 186, 205, 0.11);
}

.primary {
  color: #FE3362;
  border-color: #FE3362;
}

.gray {
  color: #B6C0CB;
  border-color: #B6C0CB;
}

.light-gray {
  color: #F4F7FB;
  border-color: #F4F7FB;
}

.dark-gray {
  color: #848E99;
  border-color: #848E99;
}

.white {
  color: #ffffff;
  border-color: #ffffff;
}

.black {
  color: #26323F;
  border-color: #26323F;
}

.white-bg {
  background: #ffffff;
}

.black-bg {
  background: #26323F;
}

.gray-bg {
  background: #B6C0CB;
}

.light-gray-bg {
  background: #F4F7FB;
}

.dark-gray-bg {
  background: #848E99;
}

.primary-bg {
  background: #FE3362;
}

.sky-bg {
  background: #F4F7FB;
}

a, button {
  transition: 300ms;
  background: none;
  border: none;
}

.icon-button {
  background: none;
  border: none;
  font-size: 16px;
  color: #B6C0CB;
}

.carousel-section {
  height: 100%;
}

.owl-carousel {
  position: relative;
  height: 100%;
  font-family: 'Museo Sans Cyrl';
}

.slide-count {
  left: 120px;
  bottom: 45px;
}

@media only screen and (max-width: 767px) {
  .slide-count {
    bottom: 30px;
    left: 50%;
  }
}

.owl-theme .owl-nav {
  margin-top: 10px;
  position: absolute;
  right: 120px;
  bottom: 45px;
}

@media only screen and (max-width: 767px) {
  .owl-theme .owl-nav {
    margin-top: 10px;
    position: absolute;
    display: flex;
    justify-content: space-between;
    bottom: 25px;
    width: 100%;
    right: 0;
    padding: 0 15px;
  }
}

.owl-theme .owl-dots {
  position: absolute;
  bottom: 45px;
  left: 152px;
  display: flex;
  text-align: center;
}

@media only screen and (max-width: 767px) {
  .owl-theme .owl-dots {
    display: none;
  }
}

.owl-theme .owl-dots .owl-dot span {
  border-radius: 0;
}

.owl-carousel .owl-nav button.owl-next,
.owl-carousel .owl-nav button.owl-prev {
  border: none;
  border-radius: 0;
  font-size: 18px;
  height: 25px;
  width: 25px;
  color: #ffffff;
  background: #FE3362;
}

.owl-carousel .owl-nav button.owl-prev {
  background: #B6C0CB;
}

.owl-slide1,
.owl-slide2,
.owl-slide3 {
  height: 70vh;
}

.cover {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.owl-slide1 {
  background-image: linear-gradient(to left, rgba(26, 32, 39, 0) 7.09%, rgba(32, 41, 51, 0.506293) 47.57%), url("../img/slider/image 93fhd.png");
}

.owl-slide1__rectangle {
  right: 0;
  width: auto !important;
}

.owl-slide1__header {
  display: block;
  width: 90%;
}

.owl-slide2 {
  background-color: #F4F7FB;
  background-image: url("../img/slider/Mask Group.png");
}

.owl-slide2__logo {
  width: 230px !important;
}

.owl-slide2__title {
  color: #26323F;
  width: 90%;
}

.owl-slide3 {
  background: #FE3362;
}

.owl-slide3__description {
  width: 70%;
  margin-bottom: 5rem;
}

.owl-slide3__img {
  width: 80% !important;
}

.owl-slide3 button {
  color: #ffffff;
}

.owl-controls {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
}

.owl-controls .owl-buttons div {
  position: absolute;
  top: 0;
  bottom: 0;
  display: inline-block;
  zoom: 1;
  margin: 0;
  width: 50px;
  height: 30px;
  line-height: 25px;
  text-align: center;
  font-size: .9em;
  border-radius: 3px;
  color: #ffffff;
  background: #000;
