}

.footer-bar__text {
  font-size: 12px;
  max-width: 230px;
}

.footer-bar__color-white {
  color: #ffffff;
}

.footer-bar__color-white-link {
  color: #ffffff;
  text-decoration: underline;
}

.footer-bar__color-red {
  color: #FE3362;
  text-decoration: underline;
}

.footer-bar__heading {
  color: #ffffff;
}

.footer-bar__color-gray {
  color: #B6C0CB;
}

.site-footer__nav {
