@import '../sass/_vars';

.buyout {
    &__img-header {
        background-image: linear-gradient(180deg,rgba(0, 0, 0, 0) -18.91%,rgba(0, 0, 0, 0.59) 57.57%),url(../img/buy-out/photo1.png);
        background-repeat: round;
        height: 510px;
        &__block {
            height: 100%;
            &__title {
                font-size: 2rem;
                @include respond-to(tablet) {
                    font-size: 2rem; }
                @include respond-to(phone-land) {
                    font-size: 20px; } } }

        &__list-item {
            font-size: 16px;
            @include respond-to(tablet) {
                margin-left: -30px; }
            @include respond-to(phone-land) {
                margin-left: -20px; } } }

    &__color-header {
        color: $dark-gray; }
    &__light-gray {
        background-color: $light-gray;
        &__responsive {
            &__square {
                width: 15px;
                height: 15px;
                background: $black;
                position: relative;
                margin-left: -8px; }
            &__block {
                @include respond-to(phone-land) {
                    border-left: 1px solid;
                    margin-left: 8px; } }
            &__block-none {
                @include respond-to(phone-land) {
                    margin-left: 8px; } }
            &__media-body {
                margin-top: -2px; } } }
    &__red {
        background: $primary;
        &__color-white {
            color: $white; } }
    &__text {
        max-width: 215px;
        @include respond-to(phone-land) {
            margin-bottom: 50px; } }

    &__square {
        position: relative;
        margin-top: -8px;
        &__commons {
            width: 15px;
            height: 15px;
            background: $black; } }
    &__border-bottom {
        border-bottom: 1px solid $black; } }
