
.news-card {
    &__size-text {
        font-size: 12px;
        color: $gray; }
    &__img {
        max-width: 190px;
        @include respond-to(phone-land) {
            width: 140px; }
        @include respond-to(tablet) {
            width: 140px; } }
    &__body {
        margin-top: 30px;
        @include respond-to(phone-land) {
            align-self: center;
            margin-top: 0px; }
        @include respond-to(tablet) {
            align-self: center;
            margin-top: 0px; }
        &__button {
            position: absolute;
            bottom: 7px;
            right: 23px;
            background: none;
            border: none;
            font-size: 25px;
            color: $sky; } } }

.news-card-simple {
    box-shadow: 0 4px 15px -8px #eaeaea;
    color: #26323f;
    width: 100%;
    height: 190px;
    @include respond-to(phone) {
        height: 100%; }
    &:hover {
        cursor: pointer;
        button {
            color: $primary; } } }

