.product-card-simple,
.product-card-horizontal {
    @include respond-to(phone) {
        height: 200px; }

    box-shadow: $shadow;
    height: 300px;
    color: $black;
    &:hover .info__button {
        color: $primary; }

    .info__button {
        background: none;
        border: none;
        color: $gray;
        transition: $transition; } }

.product-card-simple {
    @include respond-to(phone) {
        flex-direction: row-reverse; } }

.product-card-horizontal {
    .info {
        flex: 1;
        &__button {
            right: 37px; } }

    &__photo {
        flex: 2;
        img {
            @include respond-to(phone-land) {
                width: 100%; } } } }

